import React, { useState, useEffect } from 'react'
import { Container, Row, Col, Button, Modal, ModalHeader, ModalBody, ModalFooter, CustomInput } from 'reactstrap'
import { EditText } from 'react-edit-text'
import { Check, X } from 'react-feather'
import SubTitle from '../../components/SubTitle'

// ** Favourite games
import favouriteGames from '../../favourite-games.json'

// ** Utility
import { isMetaMaskInstalled, switchToHPBNetwork } from '@utility/metamask'

// ** Use Wallet
import { useWallet } from 'use-wallet'
import TokenAmount from 'token-amount'

// ** Contracts
import ESRTokenContract from '@contracts/ESRTokenContract'

// ** firebase
import firebase from 'firebase/app'

// ** Store & Actions
import { useSelector, useDispatch } from 'react-redux'
import { handleUpdateUser, handleOnlineStatus } from '@store/actions/auth'

// ** style
import './index.scss'

const Label = () => (
  <>
    <span className='switch-icon-left'>
      <Check size={14} />
    </span>
    <span className='switch-icon-right'>
      <X size={14} />
    </span>
  </>
)

const Profile = () => {
  const auth = useSelector(state => state.auth)
  const dispatch = useDispatch()
  const wallet = useWallet()

  const [showModal, setShowModal] = useState(false)
  const [isOnline, setOnline] = useState(auth.user.isOnline)
  const [hpbBalance, setHPBBalance] = useState(0)
  const [esrBalance, setESRBalance] = useState(0)

  useEffect(() => {
    if (isMetaMaskInstalled()) {
      const { balance } = wallet
      if (parseInt(balance) === -1) {
        switchToHPBNetwork().then(res => {
          if (res && res.status === 200) {
            setTimeout(() => wallet.connect('injected'), 500)
          }
        })
      }
    } else {
      alert('Please install MetaMask first.')
    }
  }, [])

  useEffect(() => {
    const { balance, account } = wallet
    setHPBBalance(TokenAmount.format(balance, 18, { symbol: 'xDai' }))

    if (account) {
      ESRTokenContract.methods.balanceOf(account).call().then(_esrBalance => {
        setESRBalance(TokenAmount.format(_esrBalance, 18, { symbol: 'ESR' }))
      })
    } else {
      setESRBalance('0 ESR')
    }
  }, [wallet])

  const defaultAvatars = [
    'https://firebasestorage.googleapis.com/v0/b/esportsref-94707.appspot.com/o/avatars%2Favatar_01.png?alt=media&token=763c6c06-c56f-41a9-9fcf-09f72913c2f1',
    'https://firebasestorage.googleapis.com/v0/b/esportsref-94707.appspot.com/o/avatars%2Favatar_02.png?alt=media&token=d9cf6dfc-1e6d-4bdd-acf1-a15ae01f2554',
    'https://firebasestorage.googleapis.com/v0/b/esportsref-94707.appspot.com/o/avatars%2Favatar_03.png?alt=media&token=ff45ad31-ea03-42d6-b398-78e9bd9d9682',
    'https://firebasestorage.googleapis.com/v0/b/esportsref-94707.appspot.com/o/avatars%2Favatar_04.png?alt=media&token=fd2d584c-2f6c-4b21-930c-355b190d765f',
    'https://firebasestorage.googleapis.com/v0/b/esportsref-94707.appspot.com/o/avatars%2Favatar_05.png?alt=media&token=9cbedf7e-043f-4e2b-981e-adb74b08570f',
    'https://firebasestorage.googleapis.com/v0/b/esportsref-94707.appspot.com/o/avatars%2Favatar_06.png?alt=media&token=bbafcc2e-4b14-42a5-94fb-dd94423303f2',
    'https://firebasestorage.googleapis.com/v0/b/esportsref-94707.appspot.com/o/avatars%2Favatar_07.png?alt=media&token=20558487-045c-4e63-b67d-3a58a5a059bf',
    'https://firebasestorage.googleapis.com/v0/b/esportsref-94707.appspot.com/o/avatars%2Favatar_08.png?alt=media&token=4b63999f-6c8d-416f-bef7-b5ba337a92d9',
    'https://firebasestorage.googleapis.com/v0/b/esportsref-94707.appspot.com/o/avatars%2Favatar_09.png?alt=media&token=5da60aea-05fd-48a6-90d0-7f4c685b03f8',
    'https://firebasestorage.googleapis.com/v0/b/esportsref-94707.appspot.com/o/avatars%2Favatar_10.png?alt=media&token=43772ee9-a717-4d8f-948c-4da7e4295805'
  ]

  const handleSave = ({ name, value }) => {
    const uid = firebase.auth().currentUser.uid

    firebase.database().ref('/users').child(`${uid}`).update({
      [name]: value
    })
  }

  const handleAvatarClick = (avatarURL) => {
    setShowModal(false)

    const uid = firebase.auth().currentUser.uid
    firebase.database().ref('/users').child(uid).update({
      avatarURL
    })

    dispatch(handleUpdateUser({ ...auth.user, avatarURL }))
  }

  const handleOnlineSwitch = () => {
    const uid = firebase.auth().currentUser.uid
    firebase.database().ref('/users').child(uid).update({
      isOnline: !isOnline
    })
    dispatch(handleOnlineStatus(!isOnline))

    setOnline(!isOnline)
  }

  return (
    <div className="content-body">
      <SubTitle title={"profile"} className="m-t-40" />
      <Container className='mt-3'>
        <Row>
          <Col md={4}>
            <Row>
              <Col xs={4} className="text-center">
                <Button color='dark' outline onClick={() => setShowModal(auth.user.usertype !== '1')} className='p-05 change-image'>
                  <img src={auth.user.avatarURL} alt="avatar" className="w-100 h-auto" width={100} height={100} />
                  {auth.user.usertype !== '1' && <span className="mt-1 d-block">Click to change</span>}
                </Button>
                <Modal isOpen={showModal} toggle={() => setShowModal(false)} className='modal-dialog-centered'>
                  <ModalHeader toggle={() => setShowModal(false)}>Change Your Avatar</ModalHeader>
                  <ModalBody>
                    <Row>
                      {defaultAvatars.map((avatarURL, index) => (
                        <Col xs={3} key={index}>
                          <div className='gallery-image' onClick={() => handleAvatarClick(avatarURL)}>
                            <img src={avatarURL} width={60} height={60} />
                          </div>
                        </Col>
                      ))}
                    </Row>
                  </ModalBody>
                  <ModalFooter>
                    <Button color='primary' onClick={() => setShowModal(false)}>
                      Accept
                    </Button>
                  </ModalFooter>
                </Modal>
              </Col>
              <Col xs={8}>
                <div className='font-25'>{auth.user.username}</div>
                <div className='font-16 mt-05'><strong>Registered as: </strong>{+auth.user.usertype ? 'referee' : 'player'}</div>
                <div className='status font-16 mt-05'>
                  <strong>Status: </strong>
                  <span className="online-status" style={{ color: auth.user.isOnline ? '#34D399' : '#888' }}>{auth.user.isOnline ? 'Online' : 'Invisible'}
                    <span className="status-round" style={{ backgroundColor: auth.user.isOnline ? '#34D399' : '#888' }}></span>
                  </span>
                </div>
                <div className='font-16 mt-05'>
                  <strong>Appear as invisible: </strong>
                  <CustomInput
                    type='switch'
                    label={<Label />}
                    className='custom-control-success'
                    id='icon-success'
                    name='icon-success'
                    inline
                    defaultChecked={isOnline}
                    onClick={() => handleOnlineSwitch()}
                  />
                </div>
              </Col>
            </Row>
          </Col>
          <Col md={8} className='mt-2 mt-md-0'>
            <Row>
              <Col>
                <div className='font-16 text-break'>
                  <strong>Gnosis Wallet Address: </strong>
                  <a href={`https://blockscout.com/xdai/mainnet/address/${auth.user.hpb}`} target="_blank">
                    {auth.user.hpb}
                  </a>
                </div>
                <div className="text-secondary">Please send email to <a href="mailto:support@esportsref.com">support@esportsref.com</a> if you wish to change your wallet address. Maximum one change per month.</div>
              </Col>
            </Row>
            <Row className="mt-2 font-16">
              <Col md={6}>
                <div className='mt-05'><strong>XDAI Coin Balance: </strong>{hpbBalance}</div>
                <div className='mt-05'><strong>ESR Token Balance: </strong>{esrBalance}</div>
                <div className='mt-05'><strong>KUDOS Token Balance: </strong>{0}</div>
                <div className='mt-05'><strong>ESR NFT Trophies: </strong>{0}</div>
              </Col>
              <Col md={6}>
                <div className='mt-05'><strong>Email: </strong>{auth.user.email}</div>
                <div className='mt-05'><strong>Password: </strong>********</div>
                <div className='mt-05'><strong>Confirm Password: </strong>********</div>
              </Col>
            </Row>
          </Col>
        </Row>
        <Row className="mt-3 mt-md-5 font-16">
          <Col md={6}>
            {/* Referee */}
            {auth.user.usertype === '1' ? (
              <>
                <Row>
                  <Col>
                    <div>Thumbs Up : {auth.user.thumbsUp || 0}</div>
                  </Col>
                  <Col>
                    <div>Thumbs Down : {auth.user.thumbsDown || 0}</div>
                  </Col>
                  <Col>
                    <div>Total: {(auth.user.thumbsUp - auth.user.thumbsDown) || 0}</div>
                  </Col>
                </Row>
                <Row>
                  <Col md={12}>
                    <div className="flex align-items-center mt-10 mb-10">
                      <div className="font-bold">Favourite genre:</div>
                      <EditText name="favouritegame" defaultValue={favouriteGames[auth.user.favouritegame]} className="edit-text" placeholder="click to add" onSave={handleSave} />
                    </div>
                  </Col>
                  <Col md={12}>
                    <div className="flex align-items-center mt-05">
                      <div className="font-bold">First favourite game:</div>
                      <EditText name="firstgame" defaultValue={auth.user.firstgame} className="edit-text" placeholder="click to add" onSave={handleSave} />
                    </div>
                  </Col>
                  <Col md={12}>
                    <div className="flex align-items-center mt-05">
                      <div className="font-bold">Second favourite game:</div>
                      <EditText name="secondgame" defaultValue={auth.user.secondgame} className="edit-text" placeholder="click to add" onSave={handleSave} />
                    </div>
                  </Col>
                  <Col md={12}>
                    <div className="flex align-items-center mt-05">
                      <div className="font-bold">Third favourite game:</div>
                      <EditText name="thirdgame" defaultValue={auth.user.thirdgame} className="edit-text" placeholder="click to add" onSave={handleSave} />
                    </div>
                  </Col>
                </Row>
              </>
            ) : (
              <>
                <div className='mb-1'>Add your usernames and gamer ID's so people can easily search for you</div>
                <Row>
                  <Col md={6}>
                    <div className="flex align-items-center mt-05">
                      <div className="font-bold">Twitch:</div>
                      <EditText name="twitch" defaultValue={auth.user.othernames.twitch} className="edit-text" placeholder="click to add" onSave={handleSave} />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="flex align-items-center mt-05">
                      <div className="font-bold">Epic:</div>
                      <EditText name="epic" defaultValue={auth.user.othernames.epic} className="edit-text" placeholder="click to add" onSave={handleSave} />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="flex align-items-center mt-05">
                      <div className="font-bold">Facebook:</div>
                      <EditText name="facebook" defaultValue={auth.user.othernames.facebook} className="edit-text" placeholder="click to add" onSave={handleSave} />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="flex align-items-center mt-05">
                      <div className="font-bold">Google:</div>
                      <EditText name="google" defaultValue={auth.user.othernames.google} className="edit-text" placeholder="click to add" onSave={handleSave} />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="flex align-items-center mt-05">
                      <div className="font-bold">Youtube:</div>
                      <EditText name="youtube" defaultValue={auth.user.othernames.youtube} className="edit-text" placeholder="click to add" onSave={handleSave} />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="flex align-items-center mt-05">
                      <div className="font-bold">Twitter:</div>
                      <EditText name="twitter" defaultValue={auth.user.othernames.twitter} className="edit-text" placeholder="click to add" onSave={handleSave} />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="flex align-items-center mt-05">
                      <div className="font-bold">Xbox:</div>
                      <EditText name="xbox" defaultValue={auth.user.othernames.xbox} className="edit-text" placeholder="click to add" onSave={handleSave} />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="flex align-items-center mt-05">
                      <div className="font-bold">Instagram:</div>
                      <EditText name="instagram" defaultValue={auth.user.othernames.instagram} className="edit-text" placeholder="click to add" onSave={handleSave} />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="flex align-items-center mt-05">
                      <div className="font-bold">PSN:</div>
                      <EditText name="psn" defaultValue={auth.user.othernames.psn} className="edit-text" placeholder="click to add" onSave={handleSave} />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="flex align-items-center mt-05">
                      <div className="font-bold">Telegram:</div>
                      <EditText name="telegram" defaultValue={auth.user.othernames.telegram} className="edit-text" placeholder="click to add" onSave={handleSave} />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="flex align-items-center mt-05">
                      <div className="font-bold">Nintendo:</div>
                      <EditText name="nintendo" defaultValue={auth.user.othernames.nintendo} className="edit-text" placeholder="click to add" onSave={handleSave} />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="flex align-items-center mt-05">
                      <div className="font-bold">WeChat:</div>
                      <EditText name="wechat" defaultValue={auth.user.othernames.wechat} className="edit-text" placeholder="click to add" onSave={handleSave} />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="flex align-items-center mt-05">
                      <div className="font-bold">Steam:</div>
                      <EditText name="steam" defaultValue={auth.user.othernames.steam} className="edit-text" placeholder="click to add" onSave={handleSave} />
                    </div>
                  </Col>
                  <Col md={6}>
                    <div className="flex align-items-center mt-05">
                      <div className="font-bold">Reddit:</div>
                      <EditText name="reddit" defaultValue={auth.user.othernames.reddit} className="edit-text" placeholder="click to add" onSave={handleSave} />
                    </div>
                  </Col>
                </Row>
              </>
            )}
          </Col>
          <Col md={6} className='mt-3 mt-md-0'>
            <Container className="award-container w-100 h-100 border p-2">
              <Row className="flex align-items-center">
                <Col md={8}>
                  <div className="game-statics">Game statistics:</div>
                  <div className="coming"><strong>COMING SOON!</strong></div>
                </Col>
                <Col md={4} className='mt-2 mt-md-0'>
                  <img src={require("./assets/award.png").default} alt="reward" className="w-100 h-auto" width={120} height={120} />
                </Col>
              </Row>
            </Container>
          </Col>
        </Row>
      </Container>

    </div>
  )
}

export default Profile
